import React from "react";
import PropTypes from "prop-types";

import st from "./style";

class ImageColorFilter extends React.Component {
  
  constructor(props) {
    super(props);
    this.state = {
      
    };
  }

  render() {

    return (
      <div style={{...st.container,...this.props.style}}>
        <img style={st.image} src={this.props.src} />
        <div style={st.colorFilter(this.props)} />
      </div>
    );

  }
}

ImageColorFilter.propTypes = {
  src: PropTypes.string,
  fromColor:PropTypes.string,
  toColor:PropTypes.string,
  style: PropTypes.object,
};

ImageColorFilter.defaultProps = {
  fromColor: 'rgba(240, 103, 166, 0.95)',
  toColor: 'rgba(255, 202, 103, 0)',
  style: {},
};

export default ImageColorFilter;
